"},[_c('p',[_vm._v("De acuerdo con el portal web En TIC Confío (2015), en Colombia, el Ministerio de las TIC es el ente encargado de regular la actividad digital, y aunque no hay una normativa específica para social media, sí existen leyes que protegen la información y los datos personales, así como las tecnologías de la información y la comunicación en general. Es de resaltar que, en cuanto a las redes sociales, se puede acudir al Artículo 15 de la Constitución Nacional, que hace referencia al derecho a la intimidad personal y familiar y al buen nombre. Además, el Gobierno ha trabajado por la implementación de mecanismos jurídicos como la Ley 1273 de 2009, que modificó el Código Penal y donde se incluyen delitos digitales relacionados con la comprobación del acceso ilegal al perfil de otra persona o la violación de datos personales. Igualmente, la difamación por cualquier red social o la creación de sitios web fraudulentos, al sustentarse con las debidas pruebas, pueden ser castigados.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo mt-5",attrs:{"id":"t_5_5"}},[_c('div',{staticClass:"h4"},[_vm._v("5.5 Derechos de autor de contenidos digitales ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo mt-5",attrs:{"id":"t_5_6"}},[_c('div',{staticClass:"h4"},[_vm._v("5.6 Normatividad colombiana ")])])}]

